*{
    margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    box-sizing: border-box;
    background: whitesmoke;
    height: 100vh;
}

.header{
    height: 8vh;
    background: black;
    color: whitesmoke;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.header .brand{
    flex-grow: 2;
    text-decoration: none;
}

.header a{
    flex-grow: 1;
    color: whitesmoke;
}

.header a:hover{
    color: #f5f5f5d3;
}

.main{
    height: 92vh;
}

.post{
    width: 70vw;
    border: 2px solid;
    margin: 20px auto;
}

.post>img{
    width: 100%;
}

.post>h1{
   padding: 7px;
   border-bottom: 2px solid;
}

.actions{
    border-top: 2px solid;
    padding: 10px;
    display: flex;
}

.actions>span{
    flex-grow: 1;
}

.actions>*{
    margin-right: 10px;
}